.header {
  padding: 30px 0 10px;
  box-sizing: border-box;
  margin: 0 auto;
  max-width: calc(100vw - 300px);
  border-bottom: 2px solid #3BB6E8;
  display: flex;
  justify-content: space-between;
  z-index: 1;
  position: relative;
}

